<script>
	import {
		getContactUsList,
		getProfessionalFieldList
	} from '../../apis/openapi.js';
	import {
		swiper,
		swiperSlide
	} from 'vue-awesome-swiper';
	import 'vue-awesome-swiper/node_modules/swiper/dist/css/swiper.css'; //
	import headermenu from '@/components/header/header.vue'
	import footerpage from '@/components/footer/footer.vue'
	import UTCUtil from '../../../static/js/UTCUtil.js';
	export default {
		metaInfo:window.$VueMetaInfo['home'],
		components: {
			swiper,
			swiperSlide,
			headermenu,
			footerpage,
		},
		data() {
			return {
				options: {
					// section
					//sectionsColor: ["#f00","#0f0","#00f"],
					//slidefalse
					//controlArrows: false,
					//
					verticalCentered: true,
					//
					//resize: true,
					//
					//scrollingSpeed: 700,
					//"#"idname
					anchors: ["page1", "page2", "page3"],
					//
					lockAnchors: true,
					//sectionjquery.easings
					//easing:,
					//css3 transform
					//css3: false,
					//
					//loopTop: true,
					//
					//loopBottom: true,
					//slide
					//loopHorizontal: false,
					//
					//continuousVertical: true,
					//false
					//autoScrolling: false,
					//true
					//scrollBar: true,
					//sectionpadding
					//paddingTop: "100px",
					//sectionpadding
					//paddingBottom: "100px",
					//jquery
					//fixedElements: ".nav",
					//
					//keyboardScrolling: false,
					//100
					//touchSensitivity: 5,
					//false
					//animateAnchor: false,
					//
					//recordHistory: true,
					//anchors
					menu: '.nav',
					//true
					//navigation: true,
					//leftright
					//navigationPosition: right,
					//
					//navigationTooltips: ["","",""],
					//
					//showActiveTooltip: true,
					//
					//slidesNavigation: true,
					//topbottom
					//slidesNavPosition: bottom,
					//jquery.slimscroll
					//scrollOverflow: true,
					//section
					//sectionSelector: ".section",
					//slide
					//slideSelector: ".slide"
					licenseKey: 'OPEN-SOURCE-GPLV3-LICENSE',
					afterLoad: this.afterLoad,
					scrollOverflow: true,
					scrollBar: false,
					menu: '#menu',
					// navigation: true,
					// anchors: ['page1', 'page2', 'page3'],
					// sectionsColor: ['#41b883', '#ff5f45', '#0798ec', '#fec401', '#1bcee6', '#ee1a59', '#2c3e4f', '#ba5be9',
					// 	'#b4b8ab'
					// ]
				},
				// bannerList: [{
				// 		bannerUrl: require("../../../static/image/home/banner-one.png"),
				// 	},
				// 	{
				// 		bannerUrl: require("../../../static/image/home/banner-two.png"),
				// 	},
				// 	{
				// 		bannerUrl: require("../../../static/image/home/banner-three.png"),
				// 	},
				// ],
				swiperOptions: {
					loop: true,
					speed: 2000,
					autoplay: {
						delay: 3000,

						stopOnLastSlide: false,
						disableOnInteraction: false,
					},
					// 
					pagination: {
						el: ".swiper-pagination",
						clickable: true, //
					},
					// 
					navigation: {
						nextEl: ".swiper-button-next",
						prevEl: ".swiper-button-prev",
					},
				},
				showBannerAnimation: true, //banner
				showAboutYourong: false, //
				showFieldAnimation: false, //
				showEliteAnimation: false, //
				showContactAnimation: false, //
				enterpriseInfo: {}, //
				fieldList: [], //
				isDirection: true,
				isScroll:false,//
			}
		},
		computed: {
			swiper() {
				return this.$refs.bannerSwiper.swiper;
			}
		},
		// watch: {
		// 	options: {
		// 		handler(newName, oldName) {
		// 			console.log(this.$refs.fullpage);
		// 		},
		// 		immediate: true
		// 	}
		// },
		mounted() {
			this.getProfessionalFieldList()
			if (this.$store.state.companyInfo) {
				this.enterpriseInfo = this.$store.state.companyInfo
			} else {
				this.getContactUsList()
			}
			setTimeout(()=>{
				this.$forceUpdate();
			},1000)
		},
		methods: {
			//
			getProfessionalFieldList() {
				let params = {
					page: 1,
					rows: 12
				}
				getProfessionalFieldList(params).then(res => {
					if (res.data.success == 'true') {
						this.fieldList = res.data.data.data
					}
				});
			},
			//
			getContactUsList() {
				getContactUsList({}).then(res => {
					if (res.data.success == 'true') {
						this.enterpriseInfo = res.data.data
					}
				});
			},
			afterLoad(anchors, item) {
				if (item.index == 0) {
					this.showBannerAnimation = true
					this.showAboutYourong = false
					this.showFieldAnimation = false
					this.showEliteAnimation = false
					this.showContactAnimation = false
				} else if (item.index == 1) {
					this.showBannerAnimation = false
					this.showAboutYourong = true
					this.showFieldAnimation = false
					this.showEliteAnimation = false
					this.showContactAnimation = false
				} else if (item.index == 2) {
					this.showBannerAnimation = false
					this.showAboutYourong = false
					this.showFieldAnimation = true
					this.showEliteAnimation = false
					this.showContactAnimation = false
				} else if (item.index == 3) {
					this.showBannerAnimation = false
					this.showAboutYourong = false
					this.showFieldAnimation = false
					this.showEliteAnimation = true
					this.showContactAnimation = false
				} else {
					this.showBannerAnimation = false
					this.showAboutYourong = false
					this.showFieldAnimation = false
					this.showEliteAnimation = false
					this.showContactAnimation = true
				}
			},
			//
			toggleNavigation(url, uuid) {
				if (this.$route.path == url) {
					this.$router.go(0)
				}
				this.$router.push({
					path: url,
					query: {
						fieldUuid: uuid
					}
				});
			},
			//
			toContactUs(id) {
				this.$router.push({
					path: 'contact-us',
					query: {
						id: id,
					}
				});
				//this.$router.go(0)

			},
			//
			toProfessional(uuid) {
				this.$router.push('/professional-field-details/'+uuid)
			},
			goAnchor(selector) {
				this.$refs.fullpage.api.moveTo(selector);
			},

		}
	}
</script>
<style>
	#myVideo {
		position: absolute;
		right: 0;
		bottom: 0;
		top: 0;
		right: 0;
		width: 100%;
		height: 100vh;
		background-size: 100% 100%;
		background-color: black;
		/* in case the video doesn't fit the whole page*/
		background-image:
			/* our video */
		;
		background-position: center center;
		background-size: contain;
		object-fit: cover;
		/*cover video background */
		z-index: 3;
	}

	.home-pc .banner-box .swiper-pagination-bullet {
		width: 12px !important;
		height: 12px !important;
		background: rgba(255, 255, 255, 1) !important;
		border-radius: 10px !important;
	}

	.home-pc .banner-box .swiper-pagination-bullet-active {
		width: 12px !important;
		height: 12px !important;
		background: #FFFFFF !important;
		border-radius: 10px !important;
	}

	.home-pc .banner-box .swiper-pagination-bullets {
		bottom: 30px !important;
		position: absolute;
		text-align: center;
	}
</style>
<style lang="scss" scoped>
	@import "./css/big-screen.scss";
    @import "./css/middle-screen.scss";
	@import "./css/apple-screen.scss";
	@import "./css/small-screen.scss";
	@import "./css/animation.scss";
</style>
