<script>
	import {
		getContactUsList,
		newsList
	} from '../../apis/openapi.js';
	import UTCUtil from '../../../static/js/UTCUtil.js';
	import pagination from '@/components/pagination';
	export default {
		components: {
			pagination
		},
		data() {
			return {
				totalCount: 100, //
				newsAllList: [],
				params: {
					page: 1,
					rows: 3,
					categoryUuid: ''
				},
				listIndex: 0,

			}
		},
		mounted() {
			if (this.$store.state.categoryInfo) {
				let categoryList = this.$store.state.categoryInfo
				this.params.categoryUuid = categoryList.find((item) => item.categoryName == '').uuid
				this.newsList()
			}
		},
		methods: {
			toMouseenter(index) {
				this.listIndex = index;
			},
			toMouseleave(index) {
				this.listIndex = index;
			},
			//
			newsList() {
				newsList(this.params).then(res => {
					if (res.data.success == 'true') {
						this.newsAllList = res.data.data.data
						this.totalCount = res.data.data.total
						this.newsAllList.forEach(item => {
							if (item.publishTime) {
								item.years = UTCUtil.showLocalDate(item.publishTime, 'YYYY/mm');
								item.day = UTCUtil.showLocalDate(item.publishTime, 'dd');
							}
						})
					}
				});
			},
			toNewsDetails(uuid) {
				this.$router.push({
					path: '/news-center-app',
					query: {
						newsUuid: uuid,
						categoryUuid:this.$route.query.categoryUuid,
					}
				});
			},
			//
			handleCurrentChange(val) {
				this.params.page = val;
				this.newsList()
			},


		}
	}
</script>
